const loading = '图片加载中';
const nullImg = '图片路径为空';
const loadErr = '加载失败\r\n点击重试';
const errPlaceholder = 'errPlaceholder';
const isErr = 2;
const isLoading = 1;
export default {
	props: {
		imageUrl: {
			type: String,
			default: ''
		},
		imageMode: {
			type: String,
			default: 'scaleToFill'
		},
		lazyLoad: {
			type: Boolean,
			default: false
		},
		imageStyle: {
			type: String,
			default: ''
		},
		passClick: {
			type: Boolean,
			default: true
		},
		width: {
			type: String,
			default: ''
		},
		height: {
			type: String,
			default: ''
		},
		radius: {
			type: String,
			default: ''
		},
		animationType: {
			type: String,
			default: 'fadIn'
		}
	},
	watch: {
		imageUrl(n, o) {
			if (n) this.init();
		}
	},
	data() {
		return {
			imagePath: '',
			imgLoadSuccess: false,
			imgStatusText: '',
			imgStatus: 0 // 0: 无
		}
	},
	created() {
		this.init();
	},
	methods: {
		init() {
			this.imgLoadSuccess = false;
			if (this.imageUrl) {
				this.imgStatusText = loading;
				this.imagePath = this.imageUrl;
			} else {
				this.imgStatusText = nullImg;
			}
		},
		imgErr() {
			this.changeStatus('error')
		},
		imgLoad() {
			this.changeStatus('success')
		},
		changeStatus(type) {
			switch (type) {
				case 'loading':
					this.checkimgLoadSuccessFalse();
					this.imgStatusText = loading;
					this.imgStatus = isLoading;
					break;
				case 'error':
					this.checkimgLoadSuccessFalse();
					if (this.imagePath !== errPlaceholder) {
						this.imgStatusText = loadErr;
						this.imgStatus = isErr;
					}
					break;
				case 'success':
					this.imgLoadSuccess = true;
					break;
			}
		},
		checkimgLoadSuccessFalse() {
			if (this.imgLoadSuccess)
				this.imgLoadSuccess = false
		},
		tryReLoad() {
			if (this.imgLoadSuccess) return;
			if (this.imgStatus === isErr && this.imagePath !== errPlaceholder) {
				this.imagePath = errPlaceholder;
				setTimeout(() => {
					this.changeStatus('loading');
					this.imagePath = this.imageUrl;
				}, 100);
			}
		},
		onTap() {
			if (this.passClick) {
				this.$emit('click');
				if (!this.imgLoadSuccess)
					this.tryReLoad();
			} else {
				if (this.imgLoadSuccess) {
					this.$emit('click');
				} else {
					this.tryReLoad();
				}
			}
		}
	}
}
